  line-height: 1.5em;
}

.vibrate-1 {
  color: yellow;
	-webkit-animation: vibrate-1 0.3s linear infinite both;
	        animation: vibrate-1 0.3s linear infinite both;
}

#restart {
  min-width: 384px;
}

#restart:hover {
  cursor: pointer;
}

#message {
  text-align: center;
}

