#wrapper {
  position: relative;
  top: 70px;
}
#header {
  text-align: center;
  padding: 10px;
}

#title {
  font-family: 'Press Start 2P', cursive;
  color: black;
  font-size: 48px;
  letter-spacing: .1em;
  padding: 10px;
}

#theme {
  font-family: 'Monoton', cursive;
