.tile-wrapper {
  height: 100%;
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.card {
  background: #fff;
  border: 4px double black;
  border-radius: 18px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
}

.card:hover {
  cursor: pointer;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.img-container {
  height: 100%;
  border-radius: 15px;
  overflow: hidden;
  text-align: center;
  background: #fff;
}

.img-container:before
  {
      content: '';
      height: 100%;
      display: inline-block;
      vertical-align: middle;
  }

img {
  margin: auto;
  width: 85%;
  display: inline-block;
  vertical-align: middle;
}

